/*** Colors ***/

$black: #212529;
$dark: #2e4049;
$grey: #788e97;
$white: #fff;
$silver: #ebedee;
$blue: #00adee;
$green: #60c5bb;
$peach: #F37C70;
$purple: #9773B3;

$white3Opacity: rgba(255, 255, 255, 0.3);
$white6Opacity: rgba(255, 255, 255, 0.6);
$white7Opacity: rgba(255, 255, 255, 0.7);
$white8Opacity: rgba(255, 255, 255, 0.8);
$black6Opacity: rgba(0, 0, 0, 0.6);
$black8Opacity: rgba(0, 0, 0, 0.8);
$blackBootstrapInsetShadow: rgba(0, 0, 0, 0.075);
$grey46Opacity: rgba($grey, 0.46);

$clickable-hover-active: $purple;

/*** End - Colors ***/

/*** Font Families ***/

$font-family-main: "Roboto", sans-serif;
$font-family-serif: "Bree Serif", serif;

/*** End - Font Families ***/

/*** Font Size ***/

$font-size-base: 62.5%;

$font-size-large: 2.8rem;
$font-size-medium: 2.2rem;
$font-size-regular: 1.4rem;
$font-size-small: 1.2rem;

/*** End - Font Size ***/

/*** Font Weight ***/

$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-bold: 700;

/*** End - Font Weight ***/

